#portfolio {
  padding-top: 120px;
  height: 50vh;
  /* display: flex;
  justify-content: center;
  flex-direction: column; */
}

#portfolio-container {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(430px, 1fr));
  grid-template-rows: auto;
  padding: 0 14vw;
}

#portfolio h3 {
  margin-bottom: 30px;
}

.view-modal {
  display: flex;
  align-items: center;
  margin: 0 4vh;
}

.overview {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}

.overview-modal {
  display: flex;
}

.overview {
  margin-bottom: 30px;
}

.overview img {
  border-radius: 15px;
  display: flex;
  justify-self: flex-end;
}

.overview-modal img {
  border-radius: 15px;
  margin-right: 20px;
}

.title-snippet h4, .overview-title-snippet h2 {
  color: #cdd9e5;
  font-weight: bold;
}

.title-snippet h4 {
  margin: 8px 0 0 20px;
  font-size: 17px;
}

div h4, .overview-title-snippet p, .title-snippet p, div h2 {
  margin: auto;
}

.overview-title-snippet p, .title-snippet p {
  color: #768390;
  word-break: normal;
  white-space: normal;
}

.title-snippet p {
  width: 235px;
  margin: 3px 0 0 20px;
  word-break: normal;
  white-space: normal;
}

.view-btn {
  font-size: 14px;
  padding: 3px 13px;
}

.exit-modal-btn {
  float: right;
  margin: auto;
  height: 30px;
  width: 30px;
  font-size: 14px;
}

.gh-ll-btns {
  margin-right: 16px;
}

.github, .live-link {
  float: right;
  margin-right: 10px;
  font-size: 14px;
  padding: 4px 13px;
}

.github a, .live-link a {
  text-decoration: none;
  color: #adbac7;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.tech-container {
  display: inline-flex;
  border: 1px solid rgba(173, 186, 199, 0.286);
  border-radius: 8px;
  margin: 8px 8px 0 0;
  padding: 0 10px;
  font-size: 14px;
}

.tech-container:hover {
  border: 1px solid rgba(180, 193, 206, 0.699);
}

.tech-container p {
  margin: 5px;
}

.project-gallery {
  max-width: 100%;
  position: relative;
  margin: auto;
  overflow-x: auto;
  width: 100%;
  animation-duration: 2s;
}

.slideshow-images {
  display: inline-flex;
  justify-content: center;
  background-color: #2b313a;
  border-radius: 18px;
  padding: 30px;
  margin-right: 20px;
}

.slideshow-images img {
  border-radius: 8px;
}

.description {
  margin-top: 20px;
}

@media screen and (max-width: 470px) {
  .title-snippet {
    /* display: none; */
    max-width: 10px;
  }

  .title-snippet p {
    width: 200px;
  }

  .title-snippet p, .title-snippet h4 {
    margin: 0 0 0 10px;
  }

  .overview img {
    margin-left: 50px;
  }

  .view-modal {
    margin: -8px;
  
  }

  .gh-ll-btns {
    float: inherit;
    margin-right: 0;
  }

  .github, .live-link {
    font-size: 12px;
    padding: 4px 5px;
  }

  .overview-title-snippet h2 {
    font-size: 22px;
  }
}

@media screen and (max-width: 560px) {
  .overview-modal {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: center;
  }

  .overview-modal img {
    display: none;
  }

  .slideshow-images img {
    height: 70vw;
  }
}